body {
  font-family: Verdana, Tahoma, Arial -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #ededed;
  margin: 0;
  padding: 0;
  font-size: 16px;
  letter-spacing: 1px;
  line-height: 25px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --content-link-c: rgb(0, 110, 255);
  --main-bc: #008080;
  --main-fc: #fff;
}

.main-bc {
  background: var(--main-bc);
  background-color: var(--main-bc);
}

.main-fc {
  color: var(--main-fc);
}

.current {
  background-color: #055;
  border-radius: 5px;
  padding: 5px;
  color: #fff;
}

.current:hover {
  background-color: #042f33;
  color: #fff;
}

.b-current {
  color: #000;
  text-shadow: 1px 1px 1px #00eeff;
  font-weight: bold;
}

.sqr-100 {
  width: 100px;
  height: 100px;
}

.sqr-100 img {
  width: 100px;
  height: 100px;
}

article nav a,
section nav a {
  color: var(--content-link-c);
}

main {
  margin-top: 60px;
}

table {
  width: 100%;
}

table.min-w-650 {
  min-width: 650px;
}

table,
tr,
th,
td {
  border: 1px solid #033;
  border-collapse: collapse;
}

.table-holder {
  width: 100%;
  overflow-x: scroll;
}

th,
td {
  padding: 4px;
}

td {
  vertical-align: top;
}

.tutorials ol {
  line-height: 29px;
}

.yt-video-holder {
  width: 100%;
  margin: auto;
  height: 600px;
}

.yt-video-holder iframe {
  width: 80%;
  height: 100%;
  margin: auto;
}

#my-apps {
  margin: 5px;
  min-width: 1200px;
  width: calc(100% - 10px);
}

#my-apps td.des {
  width: 50%;
}

#my-apps th {
  background: #033;
  border-color: black;
  color: #fff;
}

#my-apps td img {
  width: 80px;
  height: 80px;
  margin: 0;
  padding: 0;
}

#my-apps td.icon {
  vertical-align: middle;
}

.page-header {
  margin-top: 70px;
  background: var(--bc);
  padding: 5px;
  text-align: center;
  color: var(--fc);
}

div.page-header {
  background: transparent;
}

.blogs,
.source-codes,
#main,
.main-content {
  margin: 20px 20px 50px 20px;
}

.article-content,
.article-description {
  margin-top: 10px;
  margin-bottom: 20px;
}

.related-content {
  margin-top: 50px;
  padding: 2px 10px 15px 10px;
  border: 1px solid #1aaa0f;
  box-shadow: 0 0 18px 3px #6efc6e;
  border-radius: 20px;
  line-height: 40px;
}

.sc-wrapper textarea {
  width: 100%;
  height: auto;
  min-height: 500px;
  border: none;
  resize: none;
  padding: 3px;
}

.sc-nav {
  display: flex;
  flex-direction: row;
  margin-bottom: 9px;
}

.sc-nav div:not(:first-child) {
  margin-left: 15px;
}

.sc-nav div {
  background: #c9c9c9;
  color: #000;
  padding: 5px;
  border-radius: 3px;
  cursor: pointer;
  box-shadow: 0px 0px 2px 3px #ffffff;
}

.sc-nav div.btn {
  background: #0099ff;
  border-radius: 4px;
  box-shadow: 0px 0px 6px 1px #2642f8;
  color: #fff;
}

.sc-nav div.select-all {
  background: #0bb;
  box-shadow: 0px 0px 4px 2px #919090;
  color: #fff;
}

.sc-nav div.c {
  background: #00eeff;
  color: #033;
}

.feature-hdr {
  margin-top: 66px;
  padding: 150px 0 200px 0;
  background-color: #008080;
  color: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  text-align: center;
}

#contact .contact-form-holder {
  width: 100%;
  height: auto;
}

#contact iframe {
  width: 100%;
  height: 990px;
}

/* footer */

footer.footer {
  background: #008080;
  padding: 10px 10px;
  width: auto;
  color: #fff;
}

footer.footer section.container-footer {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

footer.footer section.container-footer section {
  display: flex;
  flex-direction: column;
  margin-top: 15px;
}

footer.footer section.container-footer section div {
  font-weight: bold;
}

footer.footer section.container-footer section a {
  text-align: left;
  text-decoration: none;
  color: #fff;
  margin-top: 11px;
  font-size: 12px;
}

footer div.copyright {
  text-align: center;
  margin-top: 28px;
  padding-bottom: 15px;
  font-size: 0.7rem;
}

/* End footer */

.source-space {
  margin-top: 250px;
}

/* general styles */

.italic {
  font-style: italic;
}

.quote {
  background: #f8910099;
  font-style: italic;
  color: #000;
  padding: 7px 10px;
}

.green {
  background: #01b101;
  color: #fff;
}

.title {
  font-weight: bold;
  /* font-size: 1.1rem; */
}

.disabled {
  opacity: 0.5;
  pointer-events: none;
}

.code-1 {
  font-style: italic;
  background: #000a;
  color: #ff0;
  padding: 1px;
  margin-left: 5px;
  margin-right: 5px;
  letter-spacing: 3px;
  border-radius: 3px;
}

/* end general styles */

.angle-bra::before {
  content: "> ";
}

span.create-web-app {
  color: #00a600;
}

span.update-web-app {
  color: #ff8400;
}

span.upgrade-web-app {
  color: #f00;
}

ul,
ol {
  line-height: 38px;
}

::selection {
  background: var(--bc);
  color: #fff;
}

@media only screen and (max-width: 550px) {

  .main-nav-holder {
    justify-content: space-around;
  }

  .main-nav a {
    margin-left: 0px;
  }

  #contact iframe {
    height: 1200px;
  }

  .source-space {
    margin-top: 320px;
  }

  #tutorial-list a {
    line-height: 30px;
  }

  footer.footer section.container-footer {
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-around;
    align-content: space-between;
  }

}

