#about,
#contact,
#services {
    margin-left: 15px;
    margin-right: 15px;
    margin-bottom: 80px;
    margin-top: 70px;
}

#privacy-policy {
    margin: 70px 30px 50px 30px;
}

/* for text editor */

.body-te-sfd {
    margin: 0;
    padding: 0;
    font-size: 16px;
    overflow: hidden;
}

h1.te-title {
    margin: 0;
    padding: 0;
}

header.te-sfd {
    display: flex;
    flex-direction: row;
    align-items: center;
    background: #005050;
    width: 100%;
    overflow: visible;
    height: 70px;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 70px;
}

.online-te-hdr,
.online-te-nav {
    display: flex;
    flex-direction: row;
    align-items: center;
}

header.te-sfd * {
    color: white;
}

.online-te-file-name-display {
    margin-left: 15px;
    font-size: 13px;
    display: block;
    color: #ffff00 !important;
}

.online-te-file-name-display.small-screen {
    display: none;
}


button.te-sfd {
    background-color: rgba(0, 0, 0, 0.3);
    outline: none;
    border: none;
    padding: 4px 8px;
    font-size: 15px;
    cursor: pointer;
    border-radius: 3px;
}

textarea.te-sfd {
    width: calc(100% - 20px);
    height: calc(100vh - 130px);
    padding: 10px;
    padding-bottom: 40px;
    outline: none;
    border: none;
    resize: none;
    position: absolute;
    top: 73px;
    left: 0;
    right: 0;
    bottom: 0;
    background: #6ae5f8;
}

.welcome-alert {
    position: fixed;
    top: 20px;
    width: 80vw;
    margin: 20px 8vw;
    padding: 5px 2vw;
    background: #fff;
    box-shadow: 0 0 10px 5px gray;
    border-radius: 9px;
}

.welcome-alert .close-button {
    float: right;
    background: yellow;
    padding: 2px 5px;
    margin-bottom: 10px;
    box-shadow: 0 0 5px 3px gold;
    cursor: pointer;
}

/* end Online Text Editor */

/* header menu home */
.header-menu {
    display: none;
}

/* header menu other pages */

.main-nav {
    display: none;
    flex-direction: column;
    justify-content: center;
    background: #008080;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 50px;
}

.main-nav-holder {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.main-nav a {
    text-decoration: underline;
    margin-left: 20px;
    color: #fff;
    white-space: nowrap;
    text-decoration-color: #56b2fd;
    text-decoration-thickness: 1px;
    text-decoration-style: dotted;
    text-underline-offset: 5px;
}

.nav a:hover {
    text-decoration: underline;
}

/* side menu */

.menu-icon-home {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 10px;
}

.menu-icon {
    width: 40px;
    height: 40px;
    margin-left: 10px;
    cursor: pointer;
}

.top-menu-rest {
    background: var(--bc);
    position: fixed;
    top: 5px;
    width: 100%;
    height: 40px;
    margin-bottom: 10px;
}

.menu-icon svg {
    width: inherit;
    height: inherit;
}

.menu-icon svg path {
    fill: var(--bc);
}

.top-menu-rest .menu-icon svg path {
    fill: #fff;
}

.menu-wrapper {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    opacity: 0;
    width: 0%;
    height: 100vh;
    overflow: hidden;
    background: #00505099;
    transition: width 2s, opacity 2s;
    z-index: 10;
}

.menu-wrapper-content {
    width: 50%;
    height: 100vh;
    background: #005050;
    white-space: nowrap;
}

.menu-wrapper.open {
    width: 100%;
    opacity: 1;
}

.menu-wrapper-content-hdr {
    height: 70px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 20px 15px;
}

.menu-wrapper-content-items {
    margin-left: 30px;
    margin-top: 20px;
    line-height: 32px;
    color: #fff;
}

.menu-wrapper-content-items a {
    color: #56b2fd;
}

.menu-wrapper-content-hdr img {
    width: 100px;
    height: auto;
}

.side-menu-close-btn {
    padding: 2px 9px;
    background: #ffd700;
    border-radius: 27px;
    font-size: 20px;
    cursor: pointer;
}



@media only screen and (max-width: 550px) {
    .menu-wrapper-content {
        width: 80%;
    }

    header.te-sfd {
        flex-direction: column;
        align-items: flex-start;
    }

    .online-te-file-name-display {
        display: none;
    }

    .online-te-nav {
        padding: 5px;
    }

    .online-te-hdr {
        padding-left: 12px;
        padding-top: 5px;
    }

    .online-te-file-name-display.small-screen {
        display: block;
    }

    .te-title {
        font-size: 16px;
    }
}

@media only screen and (max-height: 650px) {
    .menu-wrapper-content {
        overflow-y: scroll;
    }
}

@media screen and (min-width: 550px) {
    textarea.te-sfd {
        font-size: 17px;
    }

    .te-title {
        font-size: 25px;
        margin-left: 20px;
        margin-right: 20px;
    }

    .text-editor-js .ms-8 {
        margin-left: 15px;
    }

    .te-nav-h {
        margin-left: 20px;
    }

    .header-menu {
        display: block;
    }

    .menu-icon,
    .menu-icon-home,
    .top-menu-rest {
        display: none;
    }

    .main-nav {
        display: flex;
    }
}


/* end text editor */